<template>
  <div class="user">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-lx-cascades"></i> 用户管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button type="primary" icon="delete" class="handle-del mr10" @click="delAll">批量删除</el-button>
        <el-input v-model="select_word" placeholder="筛选关键词" class="handle-input mr10"></el-input>
        <el-button type="primary" icon="search" @click="search">搜索</el-button>
      </div>
      <el-table :data="tableData" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column prop="id" label="ID" sortable >
        </el-table-column>
        <el-table-column prop="email" label="邮箱" >
        </el-table-column>
        <el-table-column label="操作"  align="center">
          <template slot-scope="scope">
            <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="nums">
        </el-pagination>
      </div>
    </div>

    <!-- 删除提示框 -->
    <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer">
                <el-button @click="delVisible = false">取 消</el-button>
                <el-button type="primary" @click="deleteRow">确 定</el-button>
            </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'usertable',
  data() {
    return {
      nums:'100',
      tableData: [{"id":2,"email":"12@163.com"},{"id":1,"email":"1@163.com"},{"id":3,"email":"1@163.com"}],
      cur_page: 1,
      multipleSelection: [],
      select_cate: '',
      select_word: '',
      del_list: [],
      delList:[],
      is_search: false,
      editVisible: false,
      delVisible: false,
      form: {
        id:'',
        email:''
      },
      idx: -1
    }
  },
  created() {
    axios({
      method:'GET',
      url:'/admin/getTotalUser'
    }).then((res) => {
      this.nums =res.data.page
    });
    this.getData();
  },
  computed: {

  },
  methods: {
    handleCurrentChange(val) {
      this.cur_page = val;
      this.getData();
    },
    getData() {
      axios({
        url:'/admin/getUserList',
        method:'post',
        data:{
        "page": this.cur_page
        }
      }).then((res) => {
        this.tableData = Array(res.data.dataList)[0];
      })
    },
    search() {
      this.is_search = true;
    },
    formatter(row, ) {
      return row.id;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    handleEdit(index,) {
      this.idx = index;
      const item = this.tableData[index];
      this.form = {
        id: item.id,
        email: item.email,
      }
      this.editVisible = true;
    },
    handleDelete(index, ) {
      this.idx = index;
      this.delVisible = true;
    },
    delAll() {
      let str = '';
      let length = this.del_list.length;
      // for(let i=0;i<length;i++)
      this.del_list = this.del_list.concat(this.multipleSelection);
      for(let i=0;i < length;i++){
        console.log(this.del_list[i].id);
        this.delList.push(this.del_list[i].id);
      }
      console.log(this.delList);
      axios({
        url:'/admin/deleteAllUser',
        data: {
          "idList":this.delList,
        },
        method:'POST'
      }).then(res =>{
        if (res.data.code==200) {
          this.$message.error('删除了' + str);
          this.multipleSelection = [];
        }
      })

    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 确定删除
    deleteRow(){
      axios({
        method: 'post',
        url: '/admin/deleteUser',
        data: {
        "id": this.tableData[this.idx].id
      }
      }).then(res =>{
        if(res.data == 200){
          this.tableData.splice(this.idx, 1);
          this.$message.success('删除成功');
          this.delVisible = false;
        }else {
          this.$message('删除失败')
        }
      })
    }
  }
}

</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-select {
  width: 120px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}
.del-dialog-cnt{
  font-size: 16px;
  text-align: center
}
.table{
  width: 100%;
  font-size: 14px;
}
.red{
  color: #ff0000;
}
.mr10{
  margin-right: 10px;
}
</style>

